"use server"

import { query } from "@/lib/db"
import type { User } from "@/lib/types"
import bcrypt from "bcrypt"

export async function login(email: string, password: string): Promise<User | null> {
  const result: any = await query("SELECT * FROM users WHERE email = ?", [email])
  const existingUser = result[0]

  if (existingUser) {
    const passwordMatch = await bcrypt.compare(password, existingUser.password)
    if (passwordMatch) {
      const { password: _, ...userWithoutPassword } = existingUser
      return userWithoutPassword
    }
  }
  return null
}

export async function signup(email: string, password: string, name: string): Promise<User | null> {
  const result: any = await query("SELECT * FROM users WHERE email = ?", [email])
  const existingUser = result[0]

  if (existingUser) {
    return null
  }

  const hashedPassword = await bcrypt.hash(password, 10)
  const insertResult: any = await query(
    "INSERT INTO users (name, email, password, provider) VALUES (?, ?, ?, ?)",
    [name, email, hashedPassword, "email"],
  )

  if (insertResult.affectedRows > 0) {
    const newUser: User = {
      id: insertResult.insertId.toString(),
      email,
      name,
      provider: "email",
      createdAt: new Date().toISOString(),
    }
    return newUser
  }

  return null
}
